body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#page-preloader {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background: #fff;
  z-index: 100500;
}

#page-preloader .spinner {
  width: 64px;
  height: 64px;
  position: absolute;
  left: 50%;
  top: 50%;
  background: url('/images/preloader.gif') no-repeat 50% 50%;
  margin: -32px 0 0 -32px;
}

.view-container {
  margin-top:20px;
}

.book-list {
  margin-bottom: 5px;
}

.quad-container {
  min-height: 438px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.quad-container img {
  width:100%;
}

.itemButton {
  margin-top: 20px;
  text-align: center;
}

.item-checkout .first-column-checkout {
  width: 25%;
}

.dropdown .btn-inverse {
  background:#50a8e1;
  border:1px solid #2d6898;
}

.dropdown .btn-inverse:hover {
  background:rgba(80,168,225,.9);
}

.btn-inverse {
  background-color: #363636;
  color: #fff;
  border-radius: 0;
  
}
.column {
  border-bottom:1px dashed #E5E5E5;
  display: -ms-flexbox;
  display: flex;
}

.column .ab-details-title {
  font-size: 1.2em;
  line-height: 50px;
  color: #666;
}
.ab-details-info {
  line-height: 55px;
  margin-left: 10px;
  font-size:12px;
  color: #777;
  text-shadow: 1px 1px 1px rgba(255, 255, 255, 1);
}  

.search-block {
  text-align: center;
  margin-top: 10px;
}

.table {
  text-align: center;
}
.table td {
  vertical-align: middle;
}

.quadcopter-sidebar h3 {
  margin-top: 10px;
  text-align: center;
}

.basket-sidebar .btn {
  margin: 5px 0;
}

.load-more .btn {
  margin: 5px 0;
}

.delete-cart {
  padding: 2px 5px;
  cursor: pointer;
  font-weight: bold;
}

.delete-cart:hover {  
  color: red;
}



